// components/Chat.js

import React, { useMemo, useEffect, useState, useRef } from 'react';
import { 
  collection, 
  query, 
  orderBy, 
  onSnapshot, 
  addDoc, 
  serverTimestamp,
  doc,
  updateDoc
} from 'firebase/firestore';
import { db } from '../firebase';
import { useAuth } from '../context/AuthContext';
import getRecipientEmail from '../utils/getRecipientEmail';
import Message from './Message';
import ChatHeader from './ChatHeader';

const Chat = ({ chatId, users }) => {
  const { user: userLoggedIn } = useAuth();
  const [messages, setMessages] = useState([]);
  const [inputMessage, setInputMessage] = useState('');
  const [loading, setLoading] = useState(true);
  const messagesEndRef = useRef(null);

  // ===================================
  // ✅ MEMOIZED RECIPIENT CALCULATION
  // ===================================
  const recipientEmail = useMemo(() => {
    if (!users || !userLoggedIn) return null;
    
    const result = getRecipientEmail(users, userLoggedIn);
    console.log(`✅ [Chat ${chatId}] Recipient calculated:`, result);
    return result;
  }, [users, userLoggedIn, chatId]);

  // ✅ MEMOIZED SELF-CHAT CHECK
  const isSelfChat = useMemo(() => {
    return recipientEmail === userLoggedIn?.email;
  }, [recipientEmail, userLoggedIn?.email]);

  // ===================================
  // 📨 LOAD MESSAGES
  // ===================================
  useEffect(() => {
    if (!chatId) {
      setLoading(false);
      return;
    }

    console.log(`📨 [Chat ${chatId}] Loading messages...`);
    setLoading(true);

    const messagesRef = collection(db, 'chats', chatId, 'messages');
    const q = query(messagesRef, orderBy('timestamp', 'asc'));

    const unsubscribe = onSnapshot(
      q,
      (snapshot) => {
        const msgs = snapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        setMessages(msgs);
        setLoading(false);
        
        // Auto-scroll to bottom
        setTimeout(() => {
          messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
        }, 100);
      },
      (error) => {
        console.error(`❌ [Chat ${chatId}] Error loading messages:`, error);
        setLoading(false);
      }
    );

    return () => {
      console.log(`🔌 [Chat ${chatId}] Unsubscribing from messages`);
      unsubscribe();
    };
  }, [chatId]);

  // ===================================
  // 📤 SEND MESSAGE
  // ===================================
  const sendMessage = async (e) => {
    e.preventDefault();
    
    if (!inputMessage.trim() || !chatId || !userLoggedIn?.email) {
      return;
    }

    const messageText = inputMessage.trim();
    setInputMessage(''); // Clear input immediately

    try {
      const messagesRef = collection(db, 'chats', chatId, 'messages');
      
      await addDoc(messagesRef, {
        message: messageText,
        user: userLoggedIn.email,
        timestamp: serverTimestamp(),
        status: 'sent'
      });

      // Update chat's lastMessage
      const chatRef = doc(db, 'chats', chatId);
      await updateDoc(chatRef, {
        lastMessage: messageText,
        lastMessageTime: serverTimestamp()
      });

      console.log(`✅ [Chat ${chatId}] Message sent successfully`);
    } catch (error) {
      console.error(`❌ [Chat ${chatId}] Error sending message:`, error);
      setInputMessage(messageText); // Restore message on error
    }
  };

  // ===================================
  // 🚫 HANDLE INVALID STATES
  // ===================================
  if (!chatId) {
    return (
      <div className="chat-container no-chat">
        <p>Select a chat to start messaging</p>
      </div>
    );
  }

  if (!recipientEmail) {
    console.warn(`⚠️ [Chat ${chatId}] No recipient found, skipping render`);
    return (
      <div className="chat-container error">
        <p>❌ Unable to load chat. Invalid data.</p>
      </div>
    );
  }

  // ===================================
  // 🎨 RENDER
  // ===================================
  return (
    <div className="chat-container">
      {/* Header */}
      <ChatHeader 
        recipientEmail={recipientEmail} 
        isSelfChat={isSelfChat}
        chatId={chatId}
      />

      {/* Messages */}
      <div className="messages-container">
        {loading ? (
          <div className="loading">Loading messages...</div>
        ) : messages.length === 0 ? (
          <div className="no-messages">
            <p>No messages yet. Start the conversation!</p>
          </div>
        ) : (
          messages.map((msg) => (
            <Message 
              key={msg.id} 
              message={msg}
              isOwnMessage={msg.user === userLoggedIn?.email}
            />
          ))
        )}
        <div ref={messagesEndRef} />
      </div>

      {/* Input */}
      <form onSubmit={sendMessage} className="message-input-form">
        <input
          type="text"
          value={inputMessage}
          onChange={(e) => setInputMessage(e.target.value)}
          placeholder={isSelfChat ? "Note to self..." : "Type a message..."}
          className="message-input"
        />
        <button 
          type="submit" 
          disabled={!inputMessage.trim()}
          className="send-button"
        >
          Send
        </button>
      </form>
    </div>
  );
};

export default Chat;

// Styled Components (remain the same)
const Container = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 15px;
  word-break: break-word;
  transition: background-color 0.2s;

  :hover {
    background-color: ${props => props.darkMode ? '#2a2a2a' : '#f5f5f5'};
  }
`;

const UserAvatar = styled(Avatar)`
  margin: 5px;
  margin-right: 15px;
  flex-shrink: 0;
`;

const ChatContent = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-width: 0;
`;

const ChatHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
`;

const RecipientName = styled.p`
  margin: 0;
  font-weight: 600;
  font-size: 15px;
  color: ${props => props.darkMode ? '#e0e0e0' : '#000'};
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 1;
`;

const MessageTime = styled.span`
  font-size: 12px;
  color: ${props => props.darkMode ? '#8696a0' : '#667781'};
  margin-left: 8px;
  flex-shrink: 0;
`;

const LastMessageContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
`;

const LastMessage = styled.div`
  display: flex;
  align-items: center;
  gap: 4px;
  flex: 1;
  min-width: 0;
`;

const StatusWrapper = styled.span`
  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
`;

const MessageText = styled.span`
  font-size: 14px;
  color: ${props => {
    if (props.hasUnread) {
      return props.darkMode ? '#e0e0e0' : '#000';
    }
    return props.darkMode ? '#8696a0' : '#667781';
  }};
  font-weight: ${props => props.hasUnread ? '600' : '400'};
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex: 1;
`;

const UnreadBadge = styled.div`
  background-color: ${props => props.darkMode ? '#00a884' : '#25d366'};
  color: white;
  border-radius: 50%;
  min-width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
  padding: 0 6px;
  flex-shrink: 0;
`;